body {
  letter-spacing: .02em;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  font-family: inherit;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 100%;
  outline: 1px solid red;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 414px) {
  .container {
    width: 414px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

:root {
  --logo-theme-dark: #fff;
  --logo-theme-light: #494848;
  --subtitle-color: #b4b4b4;
  --accent-color: #f3c653;
  --primary-text-color: #5f5f5f;
  --secondary-text-color: #7a7a7a;
  --accent-text-color: #c79002;
  --logo-font: "Film P3";
  --main-font: "Montserrat";
  --additional-font: "Times New Roman";
  --background-lighter: #fffdee;
  --background-darker: #f0ecd6;
  --footer-background: #0e0c07;
  --transition-fast: 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-slow: 500ms cubic-bezier(.4, 0, .2, 1);
}

header {
  justify-content: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header__wrapper {
  width: 414px;
  height: 50px;
  align-items: flex-end;
  padding: 0 15px;
  display: flex;
}

.header__logo {
  letter-spacing: .3em;
  color: var(--logo-theme-dark);
  margin-top: 16px;
  margin-right: auto;
  font-family: Film P3;
  font-size: 36px;
  font-weight: 700;
  line-height: .929752;
  text-decoration: none;
  display: block;
}

.header__nav-list {
  display: flex;
}

.header__nav-icon {
  align-items: flex-start;
  display: flex;
}

.header__tel-text {
  display: none;
}

.header__nav-link {
  letter-spacing: .02em;
  color: var(--accent-color);
  align-self: baseline;
  margin-right: 6px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.21875;
}

.header__nav-items {
  align-items: center;
  margin-right: 20px;
  display: flex;
}

.header__nav-items:nth-child(2n) {
  margin-right: 0;
}

.footer {
  background: var(--footer-background);
  padding-top: 40px;
  padding-bottom: 40px;
}

@media screen and (min-width: 1024px) {
  .footer {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}

.footer .container {
  display: flex;
}

.logo-link-footer {
  font-family: var(--logo-font);
  letter-spacing: .3em;
  color: var(--accent-color);
  align-self: center;
  margin-left: 44px;
  font-size: 40px;
  font-weight: 700;
  line-height: 1.075;
}

@media screen and (min-width: 1024px) {
  .logo-link-footer {
    margin-left: 107px;
    font-size: 64px;
    line-height: 1.07813;
  }
}

@media screen and (min-width: 1280px) {
  .logo-link-footer {
    margin-left: 131px;
  }
}

.pages-list {
  margin-bottom: 32px;
}

@media screen and (min-width: 1024px) {
  .pages-list {
    margin-bottom: 0;
    margin-right: 132px;
  }
}

@media screen and (min-width: 1280px) {
  .pages-list {
    margin-right: 199px;
  }
}

.pages-link {
  letter-spacing: .02em;
  color: var(--accent-color);
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.pages-item:not(:last-child) {
  margin-bottom: 16px;
}

.footer-wrapper-right-side {
  margin-left: auto;
  padding-right: 15px;
}

@media screen and (min-width: 1024px) {
  .footer-wrapper-right-side {
    padding-right: 75px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .footer-wrapper-right-side {
    padding-right: 101px;
  }
}

.social-list-footer {
  align-items: center;
  gap: 22px;
  display: flex;
}

@media screen and (min-width: 1024px) {
  .social-list-footer {
    flex-direction: column-reverse;
    align-items: stretch;
    gap: 12px;
  }
}

.social-item-footer {
  line-height: 0;
}

@media screen and (min-width: 1024px) {
  .social-item-footer {
    min-width: 144px;
  }
}

@media screen and (min-width: 1024px) {
  .social-link {
    border: .5px solid var(--accent-color);
    border-radius: 2px;
    justify-content: space-between;
    align-items: center;
    padding: 8px 12px;
    display: flex;
  }
}

.social-link span {
  display: none;
}

@media screen and (min-width: 1024px) {
  .social-link span {
    font-family: var(--main-font);
    letter-spacing: .02em;
    color: var(--accent-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 1.25;
    display: inline;
  }
}

